//路由守卫 必须登录拿到token才能进主页

import router from './index'
import store from '@/store'

const whiteList = ['/login']
router.beforeEach((to, from, next) => {
  console.log(to.path)
  if (store.getters.token) {
    if (to.path === '/login') {
      next('/')
    } else {
      next()
    }
  } else {
    //如果没登录也能进哪个网站
    if (whiteList.includes(to.path)) {
      next()
    } else {
      next('/login')
    }
  }
})
