import { login as loginApi } from '@/api/login.js'
import router from '@/router'
import { setTokenTime } from '@/utils/auth'

export default {
  // 使用 commit 与 dispatch  修改存储的数据：
  // 如果 有   namespace: true,
  //   loginStore.dispatch("loginToken/actToken", form.value);
  //    没有
  //   loginStore.dispatch("actToken", form.value);
  namespaced: true,

  //vuex 3.获取 token 状态   state（设置数据，对象） 设置 本地存储
  // 定义state存储属性 ，定义一个属性，用来接收或者返回数据
  state: () => ({
    token: localStorage.getItem('token') || '',
    siderType: true
  }),
  //vuex  2.Mutations 用于变更store数据 突变注册 状态改变事件 同步操作 定义actions调用的mutations函数

  // commit就是调用某个mutation函数可以在组件中直接用 类似于methods方法   修改存储的数据
  mutations: {
    //创建token对象
    setToken(state, token) {
      state.token = token
      localStorage.setItem('token', token)
    },
    changeSiderType(state) {
      state.siderType = !state.siderType
    }
  },
  //vuex 1. Actions 行动 提交突变事件  专门用来异步操作   在页面组件绑定这个aciton（谁来调用
  //组件用dispatch触发 修改存储的数据
  actions: {
    login({ commit }, userinfo) {
      // console.log(userinfo)
      return new Promise((resolve, reject) => {
        // console.log(userinfo)
        loginApi(userinfo)
          .then((res) => {
            console.log(res)
            // commit就是调用某个mutation函数
            commit('setToken', res.token)
            setTokenTime()
            router.replace('/')
            resolve()
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    logout({ commit }) {
      commit('setToken', '')
      localStorage.clear()
      router.replace('/login')
    }
  }
}
